import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';

import Swal from 'sweetalert2';

import { AuthService } from '../../services/service.service';
// import { PetI } from '../../models/pet';
// import { JwtResponsePets } from '../../models/jwt-response-pets';
import { HeaderComponent } from '../header/header.component';

@Component({
  selector: 'app-pets',
  templateUrl: './pet.component.html',
  styleUrls: ['./pet.component.css'],
})
export class PetComponent implements OnInit {
  public petU: any;
  public pet: any;
  public image: any;
  public petExist: Boolean = true;
  public petSelecteded : any;
  user: boolean = false;
  raceReturn: any;
  pets: any;
  listPets: any;
  url: string = this.authService.AUTH_SERVER;
  id: number = parseInt(sessionStorage.getItem('id'));
  nameP: boolean;

  constructor(private authService: AuthService, private router: Router) {
    this.pet = {
      id: this.id,
      name: '',
      race: '',
      species: '',
      gender: '',
      vaccines: '',
      vaccinesO: '',
    };
  }

  ngOnInit() {
    console.log('url', this.url);
    if (!sessionStorage.getItem('name')) {
      Swal.fire({
        icon: 'info',
        title: 'Tienes que iniciar sesion',
        showConfirmButton: false,
        timer: 2000,
      });
      this.router.navigateByUrl('/index');
    }
    this.prevent();
    this.getJson();
    this.onShowPets();
  }

  prevent() {
    const profile = sessionStorage.getItem('profile');
    if (profile == '1') {
      return (this.user = true);
    }
  }

  onChangeRace(race: any) {
    this.raceReturn = { ...this.listPets[race] };
  }

  petSelected(nameP: string): void{
    return this.petSelecteded = this.pets.find(({ name }) => name == nameP);
  }

  onHistorialPet(id, name, imageUrl): void {
    const petsHistorial = {
      id: id,
      name: name,
      imageUrl: imageUrl,
    };
    this.authService.historialPet(petsHistorial).subscribe(
      (res) => {
        console.log('res', res);
      },
      (error) => {
        console.log('error', error);
      }
    );
  }

  closeDivs(divNomber: number): void {
    let buttonShowCreatePet = document.getElementById('buttonShowCreatePet');
    let divShowPets = document.getElementById('showPet');
    let divEditPet = document.getElementById('divFormEditPet');
    let divFormCreatePets = document.getElementById('divFormCreatePet');
    switch (divNomber) {
      case 1:
        {
          divFormCreatePets.hidden = false;
          divShowPets.hidden = true;
          buttonShowCreatePet.hidden = true;
          divEditPet.hidden = true;
        }
        break;
      case 2:
        {
          divShowPets.hidden = false;
          buttonShowCreatePet.hidden = false;
          divFormCreatePets.hidden = true;
          divEditPet.hidden = true;
        }
        break;
      case 3:
        {
          divEditPet.hidden = false;
          divShowPets.hidden = true;
          divFormCreatePets.hidden = true;
        }
        break;
      case 4:
        {
          divEditPet.hidden = true;
          divShowPets.hidden = false;
          divFormCreatePets.hidden = true;
          buttonShowCreatePet.hidden = false;
        }
        break;

      default:
        break;
    }
  }

  UpdatePet(id: number, name: string): void {
    this.closeDivs(3);
    return (this.petU = this.pets.filter((item) => item.name === name));
  }

  onShowPets() {
    this.authService.showPet(this.id).subscribe(
      (res) => {
        console.log('res', res);
        if (Object.keys(res).length === 0) {
          return this.petExist = false;
        }
        return (this.pets = res.dataPet) && (this.petExist = true);
      },
      (error) => {
        console.log('error', error);
        Swal.fire({
          icon: 'error',
          title: 'No se pueden mirar tus mascotas',
          showConfirmButton: false,
          timer: 2000,
        });
      }
    );
  }

  onUploadImage(event): void {
    let file = event.target.files[0];
    let show = `<span>Imagen seleccionada: </span>${file.name}`;
    let output = document.getElementById('selector');
    output.innerHTML = show;
    output.classList.add('active');
    return this.image = event.target.files[0];
  }

  onCreatePets(): void {
    const mascota = {
      id: this.id,
      name: this.pet.name,
      race: this.pet.race,
      species: this.pet.species,
      gender: this.pet.gender,
      age: this.pet.age,
      vaccinesO: this.pet.vaccinesO,
      vaccines: this.pet.vaccines,
    };
    let ext = this.image.name.split('.').pop();
    if (ext == 'png' || ext == 'jpg' || ext == 'jpeg' || ext == 'jfif') {
      this.authService
        .uploadImagePet(this.image, mascota.id, mascota.name)
        .subscribe(
          (res) => {
            console.log('res', res);
          },
          (error) => {
            console.log('error', error);
          }
        );
      this.authService.createPet(mascota).subscribe(
        (res) => {
          console.log('res', res);
          Swal.fire({
            icon: 'success',
            title: 'Bien hecho',
            text: 'Has creado a una nueva mascota',
            showConfirmButton: true,
          });
          return this.onShowPets();
        },

        (error) => {
          let message =
            error.error.status === 'info'
              ? 'Recuerda que ...'
              : 'Algo ha ocurrido ...';
          Swal.fire({
            icon: error.error.status,
            title: message,
            text: error.error.message,
            showConfirmButton: true,
          });
        }
      );
    } else {
      Swal.fire({
        icon: 'error',
        title: 'Error en el archivo',
        text: 'No puedes subir un archivo que no sea una imagen',
        showConfirmButton: true,
      });
      return;
    }
  }

  getJson(): void {
    // let order = [];
    this.authService.getJson().subscribe((res) => {
      this.listPets = res;
    });
  }

  onDeletePet(name: string, imageUrl: string): void {
    let dataPet = {
      id: this.id,
      name: name,
      imageUrl: '',
    };
    if (imageUrl) {
      dataPet['imageUrl'] = imageUrl;
    }
    console.log('datPet', dataPet);

    Swal.fire({
      title: '¿Estás seguro?',
      text: `Si borras a tu mascota ${dataPet.name}, se eliminaran todas las citas e historial clinico`,
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#28A745',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Estoy seguro',
    }).then((result) => {
      if (result.value) {
        return this.authService.deletePet(dataPet).subscribe(
          (res) => {
            Swal.fire({
              icon: 'success',
              title: 'Mascota borrada con exito',
              showConfirmButton: false,
              timer: 1500,
            });
            setTimeout(() => {
              this.onShowPets();
            }, 1000);
            return;
          },
          (error) => {
            console.log('error', error);
            Swal.fire({
              icon: 'error',
              title: 'No se pudo eliminar la mascota',
              showConfirmButton: false,
              timer: 1500,
            });
            return;
          }
        );
      }
    });
  }

  onUpdatePet(form): void {
    // const petUpdate = {
    //   id:         this.petU.id,
    //   name:       this.petU.name,
    //   nameU:      this.pet.name,
    // };

    console.log('petUpdate', form);
  }


  uploadImageResponse(data): void {
    Swal.fire({
      icon: 'success',
      text: 'Imagen actualizada con exito',
      showConfirmButton: false,
      timer: 3000,
    });
    setTimeout(() => {
      this.onShowPets();
    }, 3000);
  }

  getImage(nameFile: string): void {
    console.log('nameFile', nameFile);
    this.authService.getImagePet(nameFile).subscribe(
      (response) => {
        console.log('exito');
      },
      (error) => {
        console.log('error');
      }
    );
  }
}
