import { Component, OnInit }  from '@angular/core';
import { Router }             from '@angular/router';
import Swal from 'sweetalert2';

import { AuthService }       from '../../services/service.service';
import { HeaderComponent }   from '../header/header.component';

@Component({
  selector: 'app-blog',
  templateUrl: './blog.component.html',
  styleUrls: ['./blog.component.css']
})
export class BlogComponent implements OnInit {
  
  public comment:     any;
  public comments:    any;
  public id:          number =  parseInt(sessionStorage.getItem('id')) ? parseInt(sessionStorage.getItem('id')): null;
  public name:        string = sessionStorage.getItem('name');
  public dates :      any;
  constructor(private authService: AuthService, private router: Router) { 
    this.comment = {
      id:           this.id,
      name:         this.name,
      calification: '',
      description:  ''
    }; 
  }

  ngOnInit(): void {
    this.onShowComments();
    console.log('this.id', this.id);
  }

  OnCreateComent(): void {
    console.log('this.comment', this.comment);
    this.authService.comment(this.comment)
    .subscribe(
      (res) => {
        Swal.fire({
          icon: 'success',
          title: 'Exito',
          text: 'Tu comentario ha sido publicado',
          showConfirmButton: true
        });
        console.log('this.comments', this.comments);
        this.onShowComments();
      }, 
      (error) => {
        console.log('error', error);
      });
  }
  onShowComments(): void {
    this.authService.showComments()
    .subscribe(
      (res) => {
        this.comments = res;
        console.log('this.comments', this.comments);
        const calification = this.comments.map(({ calification }) => { return calification } );
        console.log('calification', calification);
      },
      (err) => {
        console.log('err', err);
      }
    )
  }

  handleMultiply (value: number) {
    return '⭐'.repeat(value);
  }

}
